.BottomCta__title {
  color: #fff;
  position: absolute;
  display: block;
  width: 46%;
  text-align: center;
  top: 50%;
  left: 50%;
  margin-left: -23%;
  margin-top: -5rem;
  z-index: 2;
}

.BottomCta__title .leti-btn {
  background: transparent;
  color: #fff;
  border-color: #fff;
  margin-top: 1rem;
  opacity: 0.5;
}

.BottomCta__img {
  overflow: hidden;
  position: relative;
}

.BottomCta__img:hover .leti-btn {
  opacity: 1;
  border-width: 0.3rem;
}

.BottomCta__img:hover .BottomCta__equipo,
.BottomCta__img:hover .BottomCta__manufactura {
  transform: scale(1.2);
}

.BottomCta__img:hover .BottomCta__equipo:before,
.BottomCta__img:hover .BottomCta__manufactura:before {
  opacity: 1;
  background: rgba(5, 90, 165, 0.8);
}

.BottomCta__equipo, .BottomCta__manufactura {
  width: auto;
  height: 50rem;
  transition: all 0.2s ease-in-out;
}

.BottomCta__equipo:before, .BottomCta__manufactura:before {
  content: '';
  width: 100%;
  height: 100%;
  display: block;
  background: rgba(5, 90, 165, 0.4);
  transition: all 0.2s ease-in-out;
  opacity: 1;
}

.BottomCta__equipo {
  background: url(../../../../images/tech-equipo.jpg) no-repeat center/cover;
}

.BottomCta__manufactura {
  background: url(../../../../images/tech-manufactura.jpg) no-repeat center center/cover;
}

@media (max-width: 576px) {
  .BottomCta__title {
    width: 86%;
    margin-left: -44%;
  }
  .BottomCta__title h2 {
    font-size: 2.2rem;
  }
  .BottomCta__title .leti-btn {
    opacity: 1;
  }
  .BottomCta__equipo, .BottomCta__manufactura {
    height: 30rem;
  }
}
